@use '../util/' as *;
// https://codepen.io/SaraSoueidan/pen/jpBbrq/?editors=1100

.toggle {
	display: grid;
	grid-template-columns: 1fr rem(48);
	gap: rem(4.8) rem(8);
	border: none;
	margin: 0;

	label {
		font-size: rem(14);
		font-weight: 700;
		color: var(--toggle);

		&[for='dark'] {
			line-height: rem(24);
			margin-right: rem(13);
		}
	}

	&__wrapper {
		position: relative;
		height: rem(24);
	}

	input[type='radio'] {
		margin: 0 rem(-2) 0 rem(-2);
		opacity: 0;
		width: rem(24);
		height: rem(24);

		&:focus ~ .toggle__button {
			border: 2px solid white;
		}
	}

	&__background {
		display: block;
		height: 100%;
		position: absolute;
		width: 100%;
		top: 0;
		border-radius: rem(12);
		background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
		pointer-events: none;
	}

	&__button {
		position: absolute;
		left: rem(3);
		top: rem(3);
		right: 100%;
		width: rem(18);
		height: rem(18);
		border-radius: 50%;
		background-color: var(--dark-bg);
		transition: all 150ms ease-in-out;
	}

	#light:checked ~ .toggle__button {
		left: calc(100% - rem(21));
		right: rem(3);
	}

	#system:checked ~ .toggle__button {
		left: 50%;
		right: auto;
		transform: translate(-50%);
	}
}
